import React from 'react';
const API_URL = process.env.REACT_APP_API_URL


class SearchResultRow extends React.Component {

    render() {
        return (
            <div class="row mb-3" key={this.props.params.uuid}>
                <div class="col-8 offset-3">
                    {/* <h5>{this.props.params.name}</h5> */}
                    <h5><a target="_blank" href={`${API_URL}/download/${this.props.params.uuid}`}>{this.props.params.name}</a></h5>
                    <small class="text-success">{this.props.params.path}</small>
                </div>
            </div>
        )
    }
    
}

export default SearchResultRow