import React from 'react';
import SearchResultRow from './SearchResultRow.js'
import EmptySearchResult from './EmptySearchResult.js';
const API_URL = process.env.REACT_APP_API_URL


class SearchForm extends React.Component {

    constructor(props){
        super(props);
        this.state = {      
            value: null,
            searchResultRows: []
        };
        this.sr = []
        this.lastQuery = ""
        this.currentQuery = ""
        this.activeQuery = false

      }

      componentDidMount() {
          this.intervalId = setInterval(this.handleSearch.bind(this), 500);
      }
      componentWillUnmount(){
        clearInterval(this.intervalId);
      }      


    handleSearch() {

        if (this.currentQuery === "" || this.currentQuery === this.lastQuery) {
            return;
        }
        if (this.activeQuery === true) {
            return;
        }
        this.activeQuery = true

        fetch(`${API_URL}/search?q=${encodeURIComponent(this.currentQuery)}`)
            .then(res => res.json())
            .then( (result) => {
                this.lastQuery = this.currentQuery
                this.activeQuery = false;
                
                this.sr = []
                result.map((item) => (
                    this.sr.push(<SearchResultRow params={item}/>)
                ))
                if (this.sr.length === 0) {
                    this.sr.push(<EmptySearchResult />)
                }

                
                this.setState({searchResultRows: this.sr});
            } );
    }

    handleInputChange(e) {
        this.currentQuery = e.target.value
    }

    render() {
        return (
            <div class="container mt-5">
                <div class="row">
                    <div class="col-6 offset-3">
                <div class="input-group mb-3">
  <input autoFocus type="text" class="form-control" placeholder="Search for files" aria-label="Search for files" aria-describedby="button-addon2" onChange={this.handleInputChange.bind(this)} />
</div>
</div>

                </div>

            <div class="row">
                {this.state.searchResultRows}
            </div>
            </div>
        )
    }
}

export default SearchForm